body {
  margin: 0;
  padding: 0;
  background-attachment: fixed;
  
}

@media (max-width: 480px) {
  body {
      background-color: black;
  }
}

@media (min-width: 481px) and (max-width: 1024px) {
  body {
    background-image: linear-gradient(
      125.26deg,
      rgba(0, 0, 0, 0.8) 34.32%,
      rgba(0, 0, 0, 0.8) 60.00%
), url('https://images.unsplash.com/photo-1574052018066-049076cbe1c5?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1473&q=80');
background-position: center center;
  background-repeat: no-repeat; 
  background-size: cover;
  -webkit-min-device-pixel-ratio: 1
  }
}
@media (min-width: 1025px) {
  body {
    background-image: linear-gradient(
      125.26deg,
      rgba(0, 0, 0, 0.8) 34.32%,
      rgba(0, 0, 0, 0.8) 60.00%
), url('https://images.unsplash.com/photo-1574052018066-049076cbe1c5?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1473&q=80');
background-position: center center;
  background-repeat: no-repeat; 
  background-size: cover;
  -webkit-min-device-pixel-ratio: 1
 }
}

